body {
  background-color: $background-color;
}

.upgrade {
  width: 100%;
  height: 90%;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  padding: 30px 20px;
  margin: 10px 0;
  .title {
    margin: 10px;
    font-size: 1.2rem;
    font-weight: 700;
  }
  .btn.upgrade-btn {
    margin: auto;
    margin-top: 20px;
    border-radius: 8px;
    font-size: 1.3rem;
    background-color: #3a2bac;
    color: white;
    outline: none;
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.25);
    &:hover {
      background-color: $main-color;
      color: white;
      transition: 0.1s all;
      box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.5);
    }
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
}
