.ui.small.modal.visible.active.main-modal.connect-modal {
  //max-width: 500px;
  .connect-group {
    display: flex;
    flex-direction: column;
    .item {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 15px 15px;
      border-radius: 8px;
      margin: 5px;
      font-weight: bold;
      background-color: rgba(black, 0.05);
      cursor: pointer;
      user-select: none;
      .connected {
        display: none;
      }
      .left-item-container {
        display: flex;
        align-items: center;
        flex-direction: row;
      }
      &.true {
        background-color: rgba($green-color, 0.2);
        .connected {
          display: block;
          width: 10px;
          height: 10px;
          border-radius: 100px;
          background-color: $green-color;
          margin-right: 10px;
        }
        &:hover {
          background-color: rgba($green-color, 0.2);
          box-shadow: inset 0 0 0 0px;
        }
      }
      &:hover {
        background-color: rgba(black, 0.1);
      }
      &:active {
        transform: scale(0.98);
      }
    }
  }
}
