.info-nav {
  position: absolute;
  z-index: 4;
  bottom: 0;
  //width: 100vw;
  opacity: 0.3;
  padding: 0px;
  right: 0;
  background: $main-color;
  box-shadow: 0px -1px 8px rgba(137, 137, 137, 0.3);
  color: white;
  font-size: 0.9rem;
  display: flex;
  justify-content: flex-end;
  border-top-left-radius: 8px;
  &.main {
    display: none;
  }
  &:hover {
    opacity: 1;
  }
  a,
  div {
    font-weight: 500;
    color: white;
    padding: 3px 10px;
    cursor: pointer;
    line-height: 16px;
    left: 3px;
    text-decoration: none;
    &:hover {
      background: rgba(white, 0.1);
    }
  }
}
.top-nav {
  position: absolute;
  z-index: 4;
  width: 100vw;
  // width:calc(100vw -250px);
  padding: 0 10px 0 60px;
  height: 55px;
  background-color: white;
  color: $main-color;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 1px 8px rgba(137, 137, 137, 0.3);
  @media (max-width: 767px) {
    padding-right: 10px;
  }
  .title {
    font-size: 1.3rem;
    margin-left: 20px;
    font-weight: 700;
    @media (max-width: 500px) {
      display: none;
    }
  }
  .left-group {
    text-align: right;
    display: flex;
    flex-direction: row;
    @media (max-width: 984px) {
      justify-content: space-between;
      .item {
        padding: 0 20px;
        display: none;
      }
    }
    .item {
      padding: 0 20px;
      .category {
        color: rgb(119, 119, 119);
      }
      .data {
        color: black;
      }
    }
  }
  .right-group {
    display: flex;
    flex-direction: row;
  }
  .item.connect-item {
    align-self: center;
    font-weight: bold;
    font-size: 0.8rem;
    padding: 7px 15px;
    background-color: rgba($main-color, 1);
    color: white;
    cursor: pointer;
    border-radius: 100px;
    text-transform: uppercase;
    &:hover {
      background-color: lighten(rgba($main-color, 1), 10%);
    }
    //&.empty-wallet {
    //  box-shadow: inset 0px 0px 0px 2px $main-color;
    //  background-color: rgba(white,1);
    //  color: $main-color;
    //  &:hover {
    //    background-color: rgba($main-color,1);
    //    color: white;
    //    opacity: 0.9;
    //  }
    //  &:active {
    //    transform: scale(0.98);
    //  }
    //}
  }
  .item {
    #language-dropdown {
      cursor: pointer;
      display: block;
      font-size: 15px;
      border: none;
      border-radius: 50px;
      color: #444;
      font-weight: bold;
      line-height: 1.02;
      padding: 0.7em 1em 0.6em 0.9em;
      max-width: 100%;
      margin-right: 10px;
      width: 68px;
      height: 34px;
      text-align: center;
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      background-image: url('https://img.icons8.com/ios-filled/50/000000/expand-arrow.png');
      background-color: rgba(0, 0, 0, 0.07);
      background-repeat: no-repeat, repeat;
      background-position: right 0.75em top 50%, 0 0;
      background-size: 0.65em auto, 100%;
      &:focus {
        border: none;
        outline: none;
      }
      &:hover {
        background-color: rgba(0, 0, 0, 0.1);
      }
    }
  }
}

.side-nav {
  position: absolute;
  z-index: 5;
  height: 100vh;
  width: 60px;
  background-color: white;
  padding-top: 5px;
  transition: width 0.3s cubic-bezier(0.05, 0.74, 0.21, 1.1);
  .item-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    .nav-logo {
      padding: 15px 10px;
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 50px;
      width: 60px;
      justify-content: center;
      margin-bottom: 70px;
      img {
        height: 30px;
      }
      .logo-title {
        // display:none;
        font-size: 0;
        opacity: 0;
        color: $main-color;
        font-weight: 700;
      }
    }
    .top-group {
    }
    .bottom-group {
      display: none;
    }

    .item {
      padding: 15px 10px;
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 50px;
      justify-content: center;
      cursor: pointer;
      width: 60px;

      &:hover {
        text-decoration: none;
      }

      img {
        height: 25px;
      }
      .nav-title {
        font-size: 0;
        // display:none;
        opacity: 0;
        color: black;
        // margin-left: 15px;
      }
      &.active {
        border-left: 3px solid white;
      }
      &:hover {
        background-color: lighten($main-color, 80%);
        transition: background-color 0.5s;
      }
    }
  }
  &:hover {
    width: 210px;
    box-shadow: 4px 0px 8px rgba(137, 137, 137, 0.7);
    .item-container {
      align-items: flex-start;
      // padding:0 5px;
    }
    .item-container .item {
      display: flex;
      flex-direction: row;
      justify-content: left;
      padding: 0 15px;
      width: 210px;

      &:hover {
        background-color: rgba(49, 131, 137, 0.05);
      }
      &.active {
        box-shadow: inset -5px 0px 0px $main-color;
        background-color: rgba(49, 131, 137, 0.05);
      }
    }
    .item-container .nav-logo {
      justify-content: left;
      width: 210px;
      padding: 0 15px;
    }

    .item-container .item .nav-title,
    .item-container .nav-logo .logo-title {
      // display:inline;
      opacity: 1;
      font-size: 1.2rem;
      padding-left: 20px;
    }

    .bottom-group {
      display: flex;
      flex-direction: row;
      width: 100%;
      .info-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        min-width: 200px;
        .info-item {
          display: flex;
          flex-direction: row;
          font-size: 12px;
          padding: 3px;
          color: $main-color;
          .info-link {
            font-weight: 600;
            color: $main-color;
            &.hover {
              color: $main-color;
            }
          }
          .info-dot {
            padding: 0 3px;
          }
        }
      }
    }
  }
}
