.trade-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  .container {
    padding: 20px 0px;
    @media (max-width: 767px) {
      padding: 20px 5px;
    }
  }
  .dashboard-session {
    padding: 10px 0px;
    .header.session-header {
      margin-left: 10px;
      text-align: left;
      font-family: 'Open Sans', 'Roboto', sans-serif;
      text-transform: uppercase;
      font-weight: 800;
      letter-spacing: 1.3px;
      color: rgba(0, 0, 0, 0.3);
      font-size: 1.1rem;
    }
  }
  .trade-info-container {
    display: flex;
    flex-direction: row;
    min-height: 183px;
    position: relative;
    padding-right: 10px;
    margin-top: 0.5rem;
    .trade-info-blocker {
      display: none;
      transition: 0.5s all ease;
    }
    &:hover {
      @media (min-width: 767px) {
        .trade-info-blocker.true {
          transition: 0.5s all ease;
          display: block;
          position: absolute;
          width: 99%;
          height: 100%;
          z-index: 4;
          background: rgba(white, 0.5);
          backdrop-filter: blur(2px);
          border-radius: 8px;
          text-align: center;
          button {
            margin: auto;
            margin-top: 60px;
            border-radius: 10px;
            font-size: 1.3rem;
            color: $main-color;
            &:hover {
              .metamask-icon {
                transform: translateY(-2px) scale(1.2);
              }
            }
            .metamask-icon {
              margin-left: 5px;
              transform: translateY(-2px) scale(1);
            }
          }
        }
      }
    }
    @media (max-width: 767px) {
      .trade-info-blocker.true {
        transition: 0.5s all ease;
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 4;
        background: rgba(white, 0.5);
        backdrop-filter: blur(2px);
        border-radius: 8px;
        text-align: center;
        button {
          margin: auto;
          margin-top: 120px;
          border-radius: 10px;
          font-size: 1.3rem;
          color: $main-color;
          &:hover {
            .metamask-icon {
              transform: translateY(-2px) scale(1.2);
            }
          }
          .metamask-icon {
            margin-left: 5px;
            transform: translateY(-2px) scale(1);
          }
        }
      }
    }
    @media screen and (max-width: map-get($breakpoints, medium)) {
      flex-direction: column;
      min-height: 350px;
      .trade-card.trade-info,
      .trade-card.trade-action {
        width: 100%;
        margin-bottom: 10px;
      }
    }
    .info-header {
      text-align: left;
      text-transform: uppercase;
      font-weight: 800;
      letter-spacing: 1.3px;
      color: rgba(0, 0, 0, 0.3);
      font-size: 1.1rem;
      padding: 0px 5px;
      margin: 0;
    }
    .trade-card {
      display: flex;
      flex-direction: column;
      background-color: white;
      justify-content: space-evenly;
      padding: 10px;
      margin: 5px;
      border-radius: 8px;
      width: 50%;
      box-shadow: 0px 1px 50px -10px rgba(107, 87, 87, 0.125);
      .trade-group {
        display: flex;
        flex-direction: row;
      }
      &.trade-info {
        .info {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          color: black;
          padding: 10px;
          padding-left: 5px;
          height: 30%;
          align-items: center;
          .title {
            font-size: 1.4rem;
            font-weight: 700;
          }
          span {
            font-family: 'Roboto Mono' !important;
            font-size: 1.3rem;
          }
        }
      }
      &.trade-action {
        background-color: #f6f6f6;
        padding: 0px;
        .btn-group {
          button {
            border-radius: 0px;
            opacity: 0.6;
            &.true {
              opacity: 1;
              color: black;
              background-color: white;
              z-index: 3;
            }
            &.left {
              border-top-left-radius: 8px;
              margin-right: 0px;
              color: $green-color;
              &.true {
                color: $green-color;
                border-top-right-radius: 8px;
              }
            }
            &.right {
              border-top-right-radius: 8px;
              margin-right: 0px;
              color: $red-color;
              &.true {
                color: $red-color;
                border-top-left-radius: 8px;
              }
            }
          }
        }
        .trade-form {
          padding: 10px;
          background-color: white;
          border-bottom-left-radius: 8px;
          border-bottom-right-radius: 8px;
          // box-shadow: 2px -2px 2px 0px rgba(0, 0, 0, 0.1);
          .max-min-btn {
            padding: 0;
            font-weight: 600;
            user-select: none;
            cursor: pointer;
            margin-right: 3px;
            opacity: 0.6;
            &:hover {
              opacity: 1;
              text-decoration: underline;
            }
          }
          .divider {
            font-weight: 200;
            user-select: none;
            cursor: pointer;
            margin-right: 3px;
            opacity: 0.6;
          }
          .max-min-btn-devider {
            margin: 0px 5px;
            margin-right: 8px;
            width: 1px;
            background: rgba(black, 0.1);
          }
          .main-input {
            border: 1px solid rgba(224, 225, 226, 0.4);
            background: rgba(0, 0, 0, 0.05);
            padding: 10px;
            border-radius: 8px;
            outline: none;
          }
          .sub-input {
            padding: 10px 3px;
            color: black;
            font-weight: 600;
            small {
              margin-left: 3px;
              opacity: 0.7;
            }
            input {
              text-align: right;
              margin-right: 5px;
              font-weight: 800;
              font-family: 'Roboto Mono' !important;
            }
          }
          .trade-action-button {
            border-radius: 8px;
            color: white;
            &.Buy {
              background: $green-color;
              &:hover {
                background: lighten($green-color, 1%);
              }
              &:active {
                background: darken($green-color, 1%);
              }
            }
            &.Sell {
              background: $red-color;
              &:hover {
                background: lighten($red-color, 5%);
              }
              &:active {
                background: darken($red-color, 5%);
              }
            }
          }
        }
      }
    }
  }
  .order-book {
    padding-right: 10px;
    height: 100%;
    .menu {
      cursor: pointer;
    }
    &.order-switcher {
      padding-top: 10px;
      .item {
        font-weight: 700;
        padding: 10px 20px;
        background: rgba(black, 0.03);
        border-radius: 100px;
        margin-left: 10px;
        &:hover {
          border-radius: 100px;
          background: rgba(black, 0.1);
        }
        &:active {
          transform: scale(0.97);
        }
        &.active {
          background: rgba($main-color, 1);
          color: white;
          border-radius: 100px;
          &:hover {
            color: white;
            background: rgba($main-color, 1);
          }
        }
      }
    }
    .orders-container {
      // padding: 5px;
      background-color: white;
      margin: 5px;
      border-radius: 8px;
      box-shadow: 0px 1px 50px -10px rgba(107, 87, 87, 0.125);
      .order-section {
        display: flex;
        flex-direction: row;
        padding: 10px;
        overflow: hidden;
        @media screen and (max-width: 768px) {
          flex-direction: column;
        }
        .book {
          width: 100%;
          padding: 10px 0px;
          margin-left: 2.5px;
          margin-right: 2.5px;
          @media screen and (max-width: 768px) {
            width: 100%;
          }
          &.sell-book {
          }
          &.buy-book {
          }
          .order-count {
            text-align: left;
            padding-left: 10px;
          }
          .order {
            user-select: none;
            background: rgba(0, 0, 0, 0.02);
            margin: 5px 0px;
            border-radius: 8px;
            padding: 10px;
            position: relative;
            overflow: hidden;
            &:hover {
              background: rgba(0, 0, 0, 0.03);
            }
            .price-row .price {
              font-weight: 500;
            }
            .wide.column {
              font-size: 1.1rem;
              &.cancel-column {
                display: flex;
                justify-content: center;
                align-items: center;
              }
            }
            .action {
              &.false .cancel-your-order-btn {
                display: none;
              }
              &.true {
                .cancel-your-order-btn {
                  text-transform: uppercase;
                  padding: 6px 15px;
                  margin: 0px;
                  font-size: 0.8rem;
                  transform: translateY(-1px);
                  border-radius: 8px;
                  @media screen and (max-width: 768px) {
                    font-size: 1rem;
                    width: 100%;
                    display: block;
                    padding: 10px 10px;
                    color: white;
                    background-color: $red-color;
                    opacity: 0.9;
                  }
                  &:hover {
                    color: white;
                    background-color: $red-color;
                    transform: scale(0.95);
                  }
                  &:active {
                    transform: scale(0.9);
                  }
                  &.disable-cancel,
                  .disable-cancel:hover,
                  .disable-cancel:active {
                    transform: none;
                    background-color: #e0e1e2;
                    color: rgba(0, 0, 0, 0.6);
                    opacity: 0.45 !important;
                    cursor: default;
                  }
                }
              }
            }
            .amount {
              // text-align: right;
              line-height: 20px;
              .light {
                opacity: 0.2;
              }
              .unit-span {
                display: inline-block;
                width: 70px;
                padding-left: 20px;
                text-align: left;
              }
              span {
                font-weight: 700;
              }
              @media screen and (max-width: 768px) {
                text-align: left;
              }
            }

            div {
              // max-width: 25%;
              text-overflow: ellipsis;
              overflow: hidden;
            }
            .sm-label {
              display: none;
              margin-bottom: 2px;
              font-weight: 700;
              @media screen and (max-width: 768px) {
                display: block;
              }
            }
            .order-id {
              text-transform: capitalize;
            }
            .user {
              // background: green;
              text-transform: capitalize;
            }
            .amount {
              // background: blue;
              text-transform: capitalize;
            }
            .price {
              text-transform: capitalize;
              // background: yellow;
            }
            &.order-header {
              font-weight: bold;
              background: none;
              @media screen and (max-width: 768px) {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}
